import React from "react";
import { node, func } from "prop-types";

const Button = ({ onClick, children }) => {
  return (
    <div
      style={{
        marginTop: "5px",
        border: "1px solid black",
        marginLeft: "5px",
        width: "90px",
        height: "50px",
        display: "inline-block"
      }}
      onClick={onClick}
    >
      {children}
    </div>
  );
};

Button.propTypes = {
  onClick: func,
  children: node.isRequired
};

Button.defaultProps = {
  onClick: () => {}
};

export default Button;
