import React, { Component, Fragment } from "react";
import GameBoard from "./components/GameBoard";
import "./Game.css";
import Button from "./components/Button";
import { ReactComponent as AND_SVG } from "./assets/images/AND.svg";
import { ReactComponent as NAND_SVG } from "./assets/images/NAND.svg";
import { ReactComponent as OR_SVG } from "./assets/images/OR.svg";
import { ReactComponent as NOR_SVG } from "./assets/images/NOR.svg";
import { ReactComponent as NOT_SVG } from "./assets/images/NOT.svg";
import { ReactComponent as XOR_SVG } from "./assets/images/XOR.svg";
import { ReactComponent as XNOR_SVG } from "./assets/images/XNOR.svg";
import GateClass, { AND, NAND, OR, NOR, NOT, XOR, XNOR } from "./Classes/Gate";
import SwitchClass from "./Classes/Switch";
import LightClass from "./Classes/Light";

class Game extends Component {
  constructor(props) {
    super(props);
    this.state = {
      gates: [],
      switches: Array.from("12345678").map(() => new SwitchClass()),
      lights: Array.from("12345678").map(() => new LightClass()),
      drawing: false,
      drawingGate: null,
      drawingI: null,
      drawingPos: { x: null, y: null }
    };
    this.clickNode = this.clickNode.bind(this);
  }

  /**
   * @param {int} gateType
   */
  addGate(gateType) {
    this.setState({ gates: [...this.state.gates, new GateClass(gateType)] });
  }

  startDrawing(coords, gate, i) {
    if (i >= 0) {
      // Node is Input
      gate.parents[i] = null;
    }

    this.setState({
      drawingPos: coords,
      drawing: true,
      drawingGate: gate,
      drawingI: i
    });
  }

  /**
   * @param {int} i - 0 for first input, 1 for second input, -1 for output
   */
  connect(node, i) {
    const { drawingI, drawingGate } = this.state;
    if (drawingI >= 0 && i === -1) {
      // Initial node is Input
      drawingGate.parents[drawingI] = node;
    } else if (drawingI === -1 && i >= 0) {
      // Initial node is Output
      node.parents[i] = drawingGate;
    }

    this.setState({
      drawingPos: { x: null, y: null },
      drawing: false,
      drawingGate: null,
      drawingI: null
    });
  }

  /**
   * @param {Object} coords x,y
   * @param {GateClass} node
   * @param {int} i - 0 for first input, 1 for second input, -1 for output
   */
  clickNode(coords, node, i) {
    const { drawing } = this.state;
    if (!drawing) {
      this.startDrawing(coords, node, i);
    } else {
      this.connect(node, i);
    }
  }

  render() {
    const {
      state: { gates, switches, lights, drawing, drawingPos }
    } = this;
    return (
      <Fragment>
        <div>
          {[
            { gateType: AND, SVG: AND_SVG },
            { gateType: NAND, SVG: NAND_SVG },
            { gateType: OR, SVG: OR_SVG },
            { gateType: NOR, SVG: NOR_SVG },
            { gateType: NOT, SVG: NOT_SVG },
            { gateType: XOR, SVG: XOR_SVG }
            // { gateType: XNOR, SVG: XNOR_SVG }
          ].map(({ gateType, SVG }) => (
            <Button onClick={() => this.addGate(gateType)}>
              <SVG />
            </Button>
          ))}
        </div>
        <GameBoard
          update={() => this.forceUpdate()}
          clickNode={(coords, gate, i) => this.clickNode(coords, gate, i)}
          gates={gates}
          switches={switches}
          lights={lights}
          drawing={drawing}
          drawingPos={drawingPos}
        />
      </Fragment>
    );
  }
}

export default Game;
